/*============================	Main Box Model		============================*/

.listingcards {
  border: 1px solid lightgrey;
  /*height: 200px;*/
  margin: 10px 0px;
  padding: 5px;
  display: flex;
}
.leftcard {
  width: 25%;
}
.leftcard img {
  width: 100%;
  height: 100%;
}
.rightcard {
  width: 75%;
  padding: 5px 10px;
}

.cardtop {
  display: flex;
  margin-bottom: 15px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  color: rgba(93, 93, 93, 255);
}
.cardtop p {
  margin: 0;
}
.cardname a {
  text-decoration: none;
  color: rgba(93, 93, 93, 255);
}
.cardname a:hover {
  text-decoration: underline;
}
.cardprice {
  margin-left: auto;
}
.carddetails {
  margin-bottom: 5px;
}
.carddetails p {
  margin: 0;
  text-align: justify;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  color: rgba(93, 93, 93, 255);
  line-height: 1;
  font-size: 13px;
}
.cardpoints {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}
.cardpickups {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.cardpickup1 {
  /*width: 20px;*/
  margin-right: 5px;
}
.cardpickup1 img {
  width: 20px;
  height: auto;
}
.cardpickup2 p {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  color: rgba(93, 93, 93, 255);
  font-size: 12px;
}
.cardbottom {
  display: flex;
  align-items: center;
}
.cardbottom p {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  color: rgba(93, 93, 93, 255);
  font-size: 12px;
}
.likenumber {
  display: flex;
  align-items: center;
  margin-left: auto;
  justify-content: center;
}
.likeimage {
  margin-right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

